@import url(https://fonts.googleapis.com/css?family=Roboto:400,700);
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


:root {
  --main-bg: #81ecec;
  --dark-grey: #20262e;
  --light-grey: rgb(46, 53, 62);
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
}

body {
  font-family: 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
  color: #20262e;
  color: var(--dark-grey);
  background-color: #81ecec;
  background-color: var(--main-bg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.container {
  max-width: 1140px;
  margin: 40px auto;
  padding: 0 5%;
}

h1 {
  text-align: center;
  font-size: 3rem;
  color: rgb(46, 53, 62);
  color: var(--light-grey);;
  text-transform: uppercase;
  line-height: 1.375;
  margin: 0 0 2.4rem 0;
  letter-spacing: 1px
}




a {
  color: #20262e;
  text-decoration: none;
}
a:hover {
  color: inherit;
  text-decoration: underline;
}

h2 { padding-top: 20px; }
h2:first-of-type { padding-top: 0; }
ul { padding: 0; }
.navbar {
  height: 60px;
  box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, .1);
  background-color: var(--light-grey);;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.navbar--logo {
  height: 25px;
  margin-left: 30px;
  width: 25px;
}
.footer {
  width: 100%;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer a,
.footer a:hover {
  font-weight: bold;
  font-style: italic;
  text-decoration: none;
}

table {
  width: 100%;
  min-width: 300px;
  margin-bottom: 2.4rem;
  background-color: var(--dark-grey);
  color: #fff;
  overflow: hidden;
}

table tr:nth-child(even) {
  background-color: var(--light-grey);;
}


table tr {
  cursor: pointer;
}

table th,
table td:before {
  color: var(--main-bg);
}

table th {
  display: none;
}

table th {
  cursor: pointer;
  position: relative;
  padding-left: 20px;
}

.sorted::before {
  content: "";
  position: absolute;
  left: 8px;
  top: 50%;
  width: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  height: 0;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
}

.desc::before {
  border-top: 5px solid var(--main-bg);
}

.asc::before {
  border-bottom: 5px solid var(--main-bg);
}

table th,
table td {
  margin: .5rem 2rem;
  text-align: left;
}



table td {
  display: block;
  font-size: 90%;
}

table td:first-child {
  padding-top: 1rem;
}

table td:last-child {
  padding-bottom: 1rem;
}

table td:before {
  content: attr(data-th) ':\A0';
  font-weight: bold;
  min-width: 8rem;
  display: inline-block;
}

@media (min-width: 600px) {
  table td:before {
    display: none;
  }
  table th,
  table td {
    display: table-cell;
  }
  table th,
  table td,
  table td:first-child,
  table td:last-child {
    padding: 1.5rem 2rem;
  }

  table th {
    padding-left: 25px;
  }
}


.photo-perpage {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}

.photo-perpage input {
  background: none;
  border: none;
  border: 1px solid var(--light-grey);
  padding: 10px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  width: 45%;
}

.photo-perpage input:focus {
  outline: none;
}

.photo-perpage button {
  background: none;
  border: none;
  background-color: var(--light-grey);;
  padding: 9px 30px;
  font-family: 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  color: var(--main-bg);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}
.pagination {
  margin: 18px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.pagination ul {
  display: inline-block;
  *display: inline;
  /* IE7 inline-block hack */

  *zoom: 1;
  margin-left: 0;
  margin-bottom: 0;
  border-radius: 3px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}
.pagination li {
  display: inline;
}
.pagination a {
  float: left;
  padding: 0 14px;
  line-height: 34px;
  text-decoration: none;
  border: 1px solid var(--dark-grey);
  border-left-width: 0;
  cursor: pointer;
  margin: 5px 0;
}
.pagination a:hover,
.pagination .active a {
  background-color:  var(--light-grey);;
  color: var(--main-bg);
}
.pagination .active a {
  color: var(--main-bg);
  font-weight: bold;
  cursor: default;
}
.pagination .disabled span,
.pagination .disabled a,
.pagination .disabled a:hover {
  color: #eee;
  background-color: transparent;
  cursor: default;
}
.pagination li:first-child a {
  border-left-width: 1px;
  border-radius: 3px 0 0 3px;
}
.pagination li:last-child a {
  border-radius: 0 3px 3px 0;
}
.pagination-centered {
  text-align: center;
}
.pagination-right {
  text-align: right;
}
.sidebar {
  position: fixed;
  right: -50vw;
  top: 0;
  height: 100%;
  background-color: var(--dark-grey);
  display: flex;
  flex-direction: column;
  -webkit-transition: all .3s;
  transition: all .3s;
  align-items: center;
  text-align: center;
  justify-content: center;
  padding: 10px 20px;
  max-width: 300px;
  color: var(--main-bg);;
}

.sidebar .id {
  border-radius: 50%;
  border: 1px solid var(--main-bg);
  width: 30px;
  margin: 2rem auto;
}

.sidebar .title {
  font-size: 2rem;
}

.sidebar img {
  width: 100%;
  margin: 20px 0;
  height: 250px;
}

.sidebar .close-btn {
  position: absolute;
  right: 20px;
  top: 20px;
  background: none;
  border: none;
  font-size: 20px;
  color: var(--main-bg);
  cursor: pointer;
  font-weight: bold;
}

.open {
  right: 0;
}
